/********************************
HERO
********************************/
#p-hero {
    position: relative;
    width: $max;
    height: 62.5%;
    padding: 0;
}

#s-hero-content {
    width: $max;
    height: 100%;
}

/********************************
HERO MEDIA
********************************/
#s-hero-media {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    width: $max;
    min-height: 100%;
    background: {
        size: cover;
        position: center;
    }

    .g-section-header {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-end;
        width: 60%;
        height: 180px;
        padding: $m4 $m7 $m10 $m8;
        background-color: transparent;
        border-radius: 4px;

        small {
            color: $liteBlue;
            font-size: 1.5rem;
            font-weight: 700;
        }

        h2 {
            color: $white;
            font-size: 3.75rem;
            font-weight: 700;
            line-height: 4.5rem;
        }
    }
}
