/********************************
TOURS
********************************/
#p-tours {
    width: $max;
    padding: 90px 0 30px 0;

    .g-container-md {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        justify-content: flex-start;
    }
}

/********************************
TOURS ITEM
********************************/
.s-tour-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 47.5%;
    min-height: 140px;
    margin: 0 5% $m6 0;
    padding: calc($m2 * 1.3);
    background-color: $white;
    border-radius: 8px;

    &:nth-of-type(2n) {
        margin: 0 0 $m6 0;
    }
}

.s-tour-item-media {
    width: 35%;
    height: 100%;
    border-radius: 8px;

    img {
        max-width: $max;
        border-radius: 8px;
    }
}

.s-tour-item-info {
    width: 60%;
    min-height: 60px;
    text-align: left;
    font-family: $fontA;

    a {
        text-decoration: none;
        color: $drkBlue;
    }

    h3 {
        margin-bottom: $m;
        color: $drkBlue;
        font-size: 1.95rem;
        font-weight: 600;
        letter-spacing: calc($ls1 * 1.5);
    }

    .g-paragraph-1 {
        color: $drkBlue !important;
        font-weight: 300;
    }
}

.s-tour-price {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    width: 60%;
    margin-bottom: calc($m / 2);

    h4 {
        margin-right: $m;
        color: $midBlue;
        font-size: 1.65rem;
        font-weight: 600;
        letter-spacing: calc($ls1 * 1.5);
    }

    span {
        color: $grey;
        font-size: 1.25rem;
        font-weight: 400;
        letter-spacing: calc($ls2 * 1.25);
    }
}
