/********************************
TOP BAR
********************************/
.g-topbar {
    // position: fixed;
    // left: 0;
    // top: 0;
    width: $max;
    background-color: $white;
    z-index: 999;

    .g-container-lg {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        min-height: 80px;
    }

    .s-nav-link,
    .menu-item {
        display: block;
        position: relative;
        text-decoration: none !important;
        color: $drkBlue !important;
        font-size: 1.5rem;
        font-weight: 500;
        letter-spacing: calc($ls2 * 1.45);
        //text-transform: uppercase;
        // &:hover,
        // &.active {
        //     color: $liteBlue !important;
        // }
    }
}

.menu-item {
    a {
        text-decoration: none;
        color: $drkBlue;
    }
}

/********************************
TOP BAR BRAND
********************************/
.s-topbar-brand {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 20%;
    height: auto;

    a {
        text-decoration: none;
    }

    img {
        display: block;
        width: 60%;
        height: auto;
        margin-right: calc($m1 * 1.5);
    }
}

/********************************
TOP BAR SOCIAL
********************************/
.s-topbar-social {
    display: none;
    align-items: center;
    justify-content: flex-start;
    width: 20%;
    height: auto;

    a {
        text-decoration: none;
    }

    i {
        margin-right: calc($m2 * 1.15);
        color: $drkBlue;
        font-size: 1.625rem;
    }
}

/********************************
TOP BAR NAV
********************************/
.s-topbar-nav {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 55%;
    height: auto;

    ul {
        display: flex;
        align-items: center;
        justify-content: center;
        width: $max;
        height: auto;
        padding: 0;
        margin: 0;
        text-align: center;
        font-family: $fontB;

        li {
            list-style-type: none;
            margin: 0 calc($m3 / 2) 0 calc($m3 / 2);
        }

        i {
            font-size: 1.65rem !important;
        }
    }

    .btn {
        float: right;
        padding: 0.6rem 2.1rem 0.775rem 2.1rem;
        background-color: $midBlue;
        color: $white !important;
        font-weight: 400;
        border-color: transparent;
        border-radius: 8px;
        -webkit-box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
        box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
        &:hover {
            background-color: $white;
            color: $drkGrey !important;
            border-color: transparent;
        }
    }
}

/********************************
TOP BAR SEARCH
********************************/
.s-topbar-search {
    width: 20%;

    ul {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: $max;
        height: auto;
        padding: 0;
        margin: 0;
        text-align: right;

        li {
            list-style-type: none;
            margin-left: $m3;
            color: rgba(104, 132, 142, 0.7);
        }

        i {
            color: $drkGrey !important;
            font-size: 2.35rem !important;
        }
    }
}

/********************************
TOP BAR AUTH
********************************/
.s-topbar-auth {
    width: 20%;
    height: auto;

    ul {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: $max;
        height: auto;
        padding: 0;
        margin: 0;
        text-align: right;
        font-family: $fontB;

        li {
            list-style-type: none;
            margin-left: $m3;
            color: rgba(104, 132, 142, 0.7);
        }

        .btn {
            float: right;
            padding: 0.6rem 2.1rem 0.775rem 2.1rem;
            background-color: $midBlue;
            color: $white !important;
            font-weight: 400;
            border-color: transparent;
            border-radius: 40px;
            -webkit-box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
            -moz-box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
            box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
            &:hover {
                background-color: $white;
                color: $drkGrey !important;
                border-color: transparent;
            }
        }
    }
}
