/********************************
INSTAGRAM BANNER
********************************/
#p-instagram-banner {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
    position: relative;
    width: $max;
    min-height: 170px;
    margin-bottom: 0;
    padding: 0;
    background-color: $white;
}

.s-insta-banner-item {
    width: 16.66%;
    height: auto;
    img {
        max-width: $max;
    }
}

.s-insta-banner-info {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 25%;
    min-height: 20px;
    margin-bottom: $m;
    padding: calc($m1 * 1.65) 0 $m1 0;
    background-color: $white;
    text-align: center;
    transform: translate(-50%, -40%);

    h4 {
        color: $drkBlue;
        font-size: 2.1rem;
        font-weight: 600;
        letter-spacing: $ls1 * 1.5;
    }
}
