/********************************
GENERAL
********************************/
input[type="text"],
input[type="email"],
input[type="password"],
input[type="date"],
input[type="range"],
textarea {
    outline: none;
    &:-webkit-autofill {
        transition: background-color 50000s ease-in-out 0s,
            color 5000s ease-in-out 0s;
    }
}

/********************************
FORM CONTROL
********************************/
.form-control {
    box-sizing: border-box;
    position: relative;
    width: $max;
    margin: 1px 0;
    height: 36px;
    line-height: 42px;
    padding: 0 0.6rem;
    font-family: $fontA;
    outline: none;
    border-radius: 0;
    -webkit-appearance: none;

    &::placeholder {
        color: $grey;
        font-family: $fontA;
        font-size: 0.85rem;
        font-weight: 400;
        letter-spacing: $ls * 1.5;
    }

    &:focus {
        color: $grey;
        font-family: $fontA;
        letter-spacing: $ls2;
        outline-style: none;
        box-shadow: none;
        border-color: transparent;
    }

    &.brd {
        color: rgba(35, 44, 56, 0.75);
    }

    &.sm {
        height: 33px;
        font-size: 1.525rem;
        line-height: 1.3;
        &::placeholder {
            font-size: 1.525rem;
        }
    }

    &.md {
        height: 42px;
        font-size: 1.6rem;
        font-weight: 300;
        &::placeholder {
            font-size: 1.625rem;
        }
    }

    &.lg {
        height: 47px;
        font-size: 1.925rem;
        &::placeholder {
            font-size: 1.925rem;
        }
    }
}

.form-control.textarea {
    height: 120px;
    padding: $m1 * 1.65 $m1 * 1.5;
    line-height: 26px;
}

#p-newsletter-cta-form,
#p-login-form {
    input[type="text"],
    input[type="email"],
    input[type="password"],
    textarea {
        outline: none;
        &:-webkit-autofill {
            transition: background-color 50000s ease-in-out 0s,
                color 5000s ease-in-out 0s;
        }
    }

    label {
        color: #505050;
        font-family: $fontB;
        font-size: 1.25rem;
        font-weight: 400;
        text-transform: uppercase;
    }

    .form-control {
        display: block;
        width: 100%;
        padding-left: calc($m2 * 1.35);
        background-color: #eaedf0;
        color: $grey;
        font-family: $fontB;
        font-size: 1.575rem;
        font-weight: 300;
        letter-spacing: $ls1;
        border-color: transparent;
        border-radius: 8px;
        //border-radius: 4px;
        -webkit-appearance: none;
        -webkit-box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
        box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);

        &:focus {
            -webkit-box-shadow: none;
            box-shadow: none;
        }
    }

    .bbm {
        height: 40px !important;
        margin-left: calc($m1 * 1.5);
        padding: 0 2.1rem 0 2.1rem;
        color: $drkWhite;
        text-align: center;
        font-family: $fontA;
        font-size: 1.55rem;
        font-weight: 400;
        line-height: 1.5;
        letter-spacing: calc($ls1 * 1.65);
        background-color: $midBlue;
        border-color: transparent;
        border-radius: 8px;
        -webkit-box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);
        box-shadow: 10px 10px 115px -19px rgba(0, 0, 0, 0.75);

        &:hover {
            background-color: $grey;
            color: $drkWhite;
            border-color: $grey;
        }
    }

    .required {
        margin-left: 2px;
        color: $blue;
        font-weight: 700;
        font-size: 1.525rem;
        letter-spacing: 0.05rem;
    }

    .not-required {
        display: none;
        color: $blue;
    }
}

#p-newsletter-cta-form {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: $max !important;
}

#p-login-form {
    width: $max;

    .form-control {
        border-color: $liteGrey;
    }

    .bbm {
        margin-left: 0;
        font-size: 1.3rem;
        text-transform: uppercase;
    }
}
