/********************************
LOGIN
********************************/
#p-login {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: $max;
    height: $max;
    padding-top: 10.5%;
}

/********************************
AUTH
********************************/
#p-auth {
    width: 30%;
    min-height: 160px;
    padding: $m3 calc($m3 * 1.3) calc($m3 * 1.3) calc($m3 * 1.3);
    background-color: $white;
    border-radius: 8px;

    h3 {
        margin-bottom: $m2 !important;
        color: $drkBlue;
        font-size: 2.7rem;
        font-weight: 600;
        letter-spacing: $ls1 * 1.5;
    }
}
