/********************************
TOUR DETAILS
********************************/
#p-tour-details {
    width: $max;
    min-height: 320px;
    padding: 50px 0 90px 0;

    h2 {
        margin-bottom: calc($m2 * 1.25);
        color: $drkBlue;
        font-family: $fontA;
        font-size: 3.95rem;
        font-weight: 700;
        letter-spacing: calc($ls1 * 1.75);
    }

    .g-paragraph-1 {
        width: 80%;
        margin-bottom: $m6;
        color: $drkBlue;
        font-size: 1.625rem;
        font-weight: 300;
        line-height: 28px;
    }
}

/********************************
TOUR OPTIONS
********************************/
.s-tour-options {
    display: none;
    align-items: center;
    justify-content: space-between;
    width: 35%;
    min-height: 20px;
    margin-bottom: $m3;
    padding: calc($m2 * 1.1) calc($m2 * 1.4);
    background-color: $liteBlue;
    border-radius: 10px;

    i {
        color: $white;
        font-size: 3.05rem;
    }
}
