/********************************
BLOG 
********************************/
#p-blog {
    position: relative;
    width: $max;
    min-height: 320px;
    padding: 0 0 90px 0;

    .g-container-md {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: flex-start;
    }
}

/********************************
BLOG ITEM
********************************/
.s-blog-item {
    position: relative;
    width: 30%;
    min-height: 140px;
    margin: 0 5% $m8 0;
    background-color: $white;
    border-radius: 8px;

    &:nth-of-type(3n) {
        margin: 0 0 $m8 0;
    }

    a {
        text-decoration: none;
    }
}

.s-blog-item-media {
    width: 100%;
    height: auto;
    border-radius: 8px;

    img {
        max-width: $max;
        border-radius: 8px 8px 0 0;
    }
}

.s-blog-item-info {
    width: 100%;
    min-height: 60px;
    padding: calc($m2 * 1.3) calc($m2 * 1.3) $m4 calc($m2 * 1.4);
    text-align: left;
    font-family: $fontA;

    h3 {
        margin-bottom: calc($m * 1.45) !important;
        color: $drkBlue;
        font-size: 1.85rem;
        font-weight: 600;
        letter-spacing: $ls1 * 1.5;
    }

    .g-paragraph-1 {
        margin-bottom: $m2;
        color: $drkBlue !important;
        font-size: 1.525rem;
        font-weight: 300;
    }
}

.s-blog-item-cat {
    width: $max;
    margin-bottom: calc($m / 2) !important;
    text-align: left;

    h6 {
        margin-bottom: calc($m * 0.75) !important;
        font-size: 1.175rem;
        font-weight: 600;
        text-transform: uppercase;
        letter-spacing: calc($ls2 * 1.1);
    }

    a {
        color: #adb6c0 !important;
    }
}

h3 {
    margin-bottom: calc($m * 1.1) !important;
    color: $drkBlue;
    font-size: 2.05rem;
    font-weight: 600;
    letter-spacing: $ls1 * 1.5;
}

.g-read {
    position: absolute;
    left: calc($m2 * 1.4);
    bottom: $m2;

    a {
        //text-decoration: none;
        color: $midBlue !important;
        font-size: 1.4rem;
        font-weight: 300;
    }
}
