/********************************
LATEST
********************************/
#s-latest-posts {
    position: relative;
    width: $max;
    min-height: 60px;
    padding: 50px 0 70px 0;

    .g-container-md {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
    }

    .g-section-header {
        margin-bottom: $m4;

        h2 {
            color: $drkBlue;
        }
    }
}

/********************************
POSTS WRAPPER
********************************/
#s-posts-wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;

    .s-blog-item {
        margin-right: 5%;

        &:last-of-type {
            margin-right: 0;
        }
    }
}
