/********************************
NEWSLETTER CTA
********************************/
.g-newsletter-cta {
    width: $max;
    height: auto;
    padding-bottom: calc($m1 * 1.5);

    &.s-single-post {
        display: none;
        margin-bottom: calc($m9 * 1.35) !important;
    }
}

/********************************
NEWSLETTER CTA CONTENT
********************************/
#s-nwsltr-cta-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: $max;
    height: auto;
    padding: $m5 $m4 $m3 $m4;
    background-color: $white;
    border-radius: 8px;
}

/********************************
NEWSLETTER CTA INFO
********************************/
#s-nwsltr-cta-info {
    width: 45%;
    height: auto;
    text-align: left;

    h2 {
        //margin-bottom: 0 !important;
        color: $drkBlue;
        font-size: 2.65rem;
        font-weight: 700;
        letter-spacing: calc($ls2 * 1.15);
    }
}

/********************************
NEWSLETTER CTA ACTION
********************************/
#s-nwsltr-cta-action {
    width: 47.5%;
    height: auto;
    padding: 0;

    .form-group,
    .form-inline {
        width: $max !important;
        margin-bottom: 0 !important;
    }
}
