/********************************
GUIDE DETAILS
********************************/
#p-guide-details {
    width: $max;
    min-height: 320px;
    padding: 80px 0 70px 0;

    h2 {
        margin-bottom: calc($m2 * 1.25);
        color: $drkBlue;
        font-family: $fontA;
        font-size: 3.5rem;
        font-weight: 700;
        letter-spacing: calc($ls1 * 1.75);
    }

    .g-paragraph-1 {
        width: 80%;
        margin-bottom: $m6;
        color: $drkBlue;
        font-size: 1.625rem;
        font-weight: 300;
        line-height: 28px;
    }
}

.s-guide-details-media {
    width: 28%;
    height: auto;
    margin-bottom: $m3;
    border-radius: 8px;

    img {
        max-width: $max;
        border-radius: 8px;
    }
}
