/********************************
GUIDES
********************************/
#p-guides {
    position: relative;
    width: $max;
    min-height: 120px;
    padding: 100px 0 50px 0;

    .g-container-md {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: flex-start;
    }
}

/********************************
GUIDE ITEM
********************************/
.s-guide-item {
    position: relative;
    width: 30%;
    min-height: 140px;
    margin: 0 5% $m7 0;
    background-color: $white;
    border-radius: 8px;

    &:nth-of-type(3n) {
        margin: 0 0 $m8 0;
    }

    a {
        text-decoration: none;
    }
}

/********************************
GUIDE MEDIA
********************************/
.s-guide-media {
    overflow: hidden;
    width: 100%;
    height: auto;
    border-radius: 8px 8px 0 0;

    img {
        max-width: $max;
        border-radius: 8px 8px 0 0;
    }
}

/********************************
GUIDE INFO
********************************/
.s-guide-info {
    width: 100%;
    min-height: 60px;
    padding: calc($m2 * 1.3) calc($m2 * 1.3) $m4 calc($m2 * 1.4);
    text-align: left;
    font-family: $fontA;

    h4 {
        margin-bottom: calc($m * 1.45) !important;
        color: $drkBlue;
        font-size: 1.85rem;
        font-weight: 600;
        letter-spacing: $ls1 * 1.5;
    }

    .g-paragraph-1 {
        margin-bottom: calc($m2 * 1.3);
        color: $drkBlue !important;
        font-size: 1.525rem;
        font-weight: 300;
    }
}

/********************************
GUIDE SOCIAL 
********************************/
.s-guide-social {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    width: 80%;
}

.s-guide-social-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    margin-right: $m2;
    background-color: rgba(98, 174, 228, 0.175);
    border-radius: 4px;

    i {
        color: $midBlue;
        font-size: 2rem;
    }
}
