/********************************
SINGLE POST
********************************/
.p-single-post {
    position: relative;
    width: $max;
    height: auto;
    margin-bottom: $m5;
    padding: 70px 0 70px 0;
}

/********************************
POST HEADER
********************************/
.s-post-header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    width: 92.5%;
    min-height: 100px;
    margin-bottom: $m4;
    padding: $m3 $m8 $m4 $m8;
    text-align: left;

    h2 {
        margin-bottom: $m4;
        color: $drkBlue;
        font-size: 3.975rem;
        font-weight: 700;
        letter-spacing: calc($ls2 * 1.5);
        //text-transform: uppercase;
        line-height: 4.85rem;
    }
}

.s-post-item-icon {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: calc($m * 1.35);
    text-align: left;

    i {
        font-size: 2.15rem;
        margin-right: calc($m1 * 1.15);
    }

    small {
        color: $liteBlue;
        font-size: 1.625rem;
        font-weight: 500;
        letter-spacing: calc($ls2 * 1.75);
    }

    i {
        color: $liteBlue;
    }
}

/********************************
POST AUTHOR
********************************/
.s-post-author {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    min-height: 40px;

    h5 {
        color: $drkGrey;
        font-size: 1.85rem;
        font-weight: 400;
        letter-spacing: calc($ls1 * 1.75);
    }
}

.s-author-thumbnail {
    width: 50px;
    height: 50px;
    margin-right: calc($m2 * 1.35);
    border-radius: 100%;

    img {
        max-width: $max;
        border-radius: $max;
    }
}

/********************************
POST CONTENT
********************************/
.s-post-content {
    width: $max;
    min-height: 240px;

    h3,
    h4 {
        color: $drkBlue;
        font-weight: 700;
    }

    h3 {
        margin-bottom: $m2;
        font-size: 3.05rem;
        letter-spacing: calc($ls2 * 1.15);
        line-height: 4.15rem;
    }

    h4 {
        margin-bottom: calc($m2 * 1.35);
        font-size: 2.5rem;
        letter-spacing: calc($ls2 * 1.45);
        line-height: 3.75rem;
    }

    .g-paragraph-1,
    blockquote {
        margin-bottom: calc($m2 * 1.25);
        color: $drkBlue !important;
        font-family: $fontA;
        font-size: 1.625rem;
        font-weight: 300;
        letter-spacing: calc($ls2 * 1.25);
        line-height: 28px;
    }

    blockquote {
        width: 90%;
        padding-left: 2rem;
        font-size: 1.65rem;
        font-weight: 500;
        font-style: italic;
        border-left: 5px solid rgba(98, 174, 228, 0.6);
    }
}

.s-intro-feat-img {
    width: $max;
    height: auto;
    margin-bottom: $m5;
}

.s-feat-img {
    width: $max;
    height: auto;
    margin-bottom: $m4;
}

.s-post-content-text {
    margin-bottom: $m6;
    padding: 0 $m8 0 $m8;
}
