/********************************
TAGS
********************************/
#s-tags {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: flex-start;
    width: $max;
    min-height: 70px;
    padding: calc($m2 * 1.6) calc($m3 * 1.15) $m3 calc($m2 * 1.15);
    background-color: $white;
}

/********************************
TAGS ITEM
********************************/
.s-tags-item {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 25px;
    margin: 0 $m $m1 calc($m1 * 1) !important;
    padding: calc($m * 1.15) $m1;
    background-color: rgba(44, 129, 253, 0.0875);

    span {
        display: block;
        color: $drkBlue;
        font-size: 1.15rem;
        font-weight: 400;
    }
}
